@import "../../common";

.content-header {
    display: inline-block;
    width: 100%;
    
    .title {
        font-size: 21px;
        font-weight: bold;
    }
    
    .title,
    .buttons > div {
        float: left;
    }
    
    .buttons {
        font-size: 10px;
        float: right;
    
        div {
            padding: 7px;
            border: 1px solid $inactive;
            background-color: $inactive;
            cursor: pointer;
        }
    
        div:hover {
            border: 1px solid $hover;
        }
    
        div:hover:active {
            opacity: 0.7;
        }
        
        div:not(:last-child) {
            margin-right: 7px;
        }
    }
}

.content-header.large {
    padding: 30px;
}

.content-header.medium {
    padding: 15px 30px;
}