@import "../../common";

.users-list {
    .list {
        margin: 0 30px;
        
        .list-header {
            display: flex;
            border-bottom: 1px solid $inactive;
            
            .header {
                float: left;
                padding: $row-padding;
                font-size: smaller;
            }
            
            .header.picker {
                width: 30px;
                text-align: center;
            }
            
            .header.id {
                width: 70px;
                text-align: center;
                cursor: pointer;
    
                span {
                    margin-right: 3px;
                }
            }
    
            .header.id:hover {
                color: $active;
            }
    
            .header.id:hover:active {
                opacity: 0.7;
            }
            
            .header.name {
                width: calc((100% - 100px) * .27);
                @include ellipsis
            }
            
            .header.about {
                width: calc((100% - 100px) * .45);
                @include ellipsis
            }
            
            .header.location {
                width: calc((100% - 100px) * .28);
                @include ellipsis
            }
        }
        
        .list-content {
            clear: both;
            
            .item-wrapper {
                clear: both;
                display: flex;
                border-bottom: 1px solid $inactive;
                
                .item {
                    float: left;
                    padding: $row-padding
                }
                
                .item.picker {
                    width: 30px;
                    text-align: center;
                }
                
                .item.id {
                    width: 70px;
                    text-align: center;
                    cursor: pointer;
                }
                
                .item.name {
                    width: calc((100% - 100px) * .27);
                    @include ellipsis;
                    cursor: pointer;
                    padding-left: 35px;
                    
                    .img {
                        float: left;
                        width: 21px;
                        height: 21px;
                        position: absolute;
                        margin-left: -30px;
                        background: $inactive url("../../images/noimage.png") no-repeat center;
                        background-size: cover;
                        border-radius: 50%;
                    }
                }
                
                .item.about {
                    width: calc((100% - 100px) * .45);
                    @include ellipsis;
                    cursor: pointer;
                }
                
                .item.location {
                    width: calc((100% - 100px) * .28);
                    @include ellipsis;
                    cursor: pointer;
                }
                
                .item.id:hover,
                .item.name:hover,
                .item.about:hover,
                .item.location:hover {
                    color: $active
                }
                
                .item.id:hover:active,
                .item.name:hover:active,
                .item.about:hover:active,
                .item.location:hover:active {
                    opacity: 0.7;
                }
            }
            
            .item-wrapper.selected {
                background-color: $inactive;
                border-bottom: 1px solid white;
            }
            
            .pagination {
                display: flex;
                justify-content: flex-end;
                padding: $row-padding;
                font-size: smaller;
                line-height: 20px;
                
                .buttons {
                    float: left;
                    
                    div {
                        float: left;
                        width: 20px;
                        height: 20px;
                        margin: 0 3px;
                        background-repeat: no-repeat;
                        background-position: center;
                        cursor: pointer;
                    }
                    
                    div:hover {
                        background-color: $inactive;
                    }
                    
                    div:hover:active {
                        opacity: 0.7;
                    }
                    
                    div[name='first'] {
                        @include flip-horizontal
                    }
                    
                    div[name='previous'] {
                        @include flip-horizontal
                    }
                    
                    div[name='previous'],
                    div[name='next'] {
                        background-image: url("nav-next.png");
                    }
                    
                    div[name='first'],
                    div[name='last'] {
                        background-image: url("nav-last.png");
                    }
                    
                    div[name='previous'].disabled,
                    div[name='next'].disabled {
                        background-image: url("nav-next-disabled.png");
                    }
                    
                    div[name='first'].disabled,
                    div[name='last'].disabled {
                        background-image: url("nav-last-disabled.png");
                    }
                }
            }
            
            .pagination > div:not(:last-child) {
                margin-right: 21px;
            }
        }
    }
}