@import "../../common";

.user-details {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    
    .modal {
        box-shadow: -1px 1px 3px white;
        border-radius: 7px;
        background-color: whitesmoke;
        display: block;
        width: 70%;
        height: 95%;
        max-height: 665px;
        overflow: hidden;
        @include middle-transform;
        
        .content-header {
            border-bottom: 1px solid $inactive;
        }
        
        .details {
            padding: 30px;
            height: calc(100% - 60px);
            overflow-y: auto;
            position: absolute;
            width: 100%;
            @include transition-enabled;
    
            .image {
                float: left;
                width: 200px;
                height: 200px;
                background: #000 url("../../images/noimage.png") no-repeat center;
                background-size: contain;
                cursor: pointer;
                overflow: hidden;
                position: relative;
                @include transition-enabled;
    
                .loading {
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.7);
                    @include middle-flex;
                    
                    .loader {
                        @include spin;
                        width: 30%;
                        height: 30%;
                        background: transparent url('../../images/loading-white.png') no-repeat center;
                        background-size: cover;
                    }
                }
            }
            
            .download {
                position: absolute;
                width: 30px;
                height: 30px;
                //background-color: rgba(0, 0, 0, 0.7);
                margin: 170px 0 0 170px;
                
                img {
                    @include middle-transform;
                    width: 70%;
                    height: 70%;
                }
            }
            
            .image:hover {
                border: 1px solid $hover;
                
                .download {
                    display: block;
                }
            }
            
            .id,
            .name {
                margin-bottom: 3px;
                
                input {
                    width: 100%;
                    @include edit-field
                }
            }
            
            .id,
            .name,
            .aboutMe {
                float: right;
                width: calc(100% - 230px)
            }
            
            .aboutMe {
                max-height: 101px;
                overflow-y: auto;
                
                textarea {
                    width: 100%;
                    height: 70px;
                    resize: none;
                    @include edit-field
                }
            }
            
            .location {
                position: absolute;
                width: calc(100% - 60px);
                top: 260px;
                height: 315px;
                background: $inactive url(../BingMap/FEMap.png) no-repeat center;
                background-size: 100%;
                
                div.edit-field {
                    background-color: rgba(0, 0, 0, 0.7);
                    color: white;
                    padding: 10px 15px 15px 15px;
                    position: absolute;
                    width: 100%;
                    top: 0;
                    
                    input {
                        margin-top: 10px;
                        @include edit-field
                    }
                    
                    .reset-location {
                        float: right;
                        width: 20px;
                        height: 20px;
                        background: transparent url(../../images/reset.png) no-repeat center;
                        background-size: cover;
                        margin-top: 14px;
                        cursor: pointer;
                    }
                    
                    .reset-location:hover:active {
                        opacity: 0.7;
                    }
                }
                
                div.loading {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    background-color: rgba(0, 0, 0, 0.7);
                    color: white;
                    font-size: 14px;
                    
                    .loader {
                        @include middle-transform;
                        @include spin;
                        width: 70px;
                        height: 70px;
                        background: transparent url('../../images/loading-white.png') no-repeat center;
                        background-size: cover;
                        margin: -35px 0 0 -35px;
                    }
                }
            }
            
            div > label {
                font-weight: bold;
                border-bottom: 1px solid $inactive;
                line-height: 25px;
            }
            
            div > label:after {
                @include clear-after
            }
        }
    }
}

textarea:focus {
    border: 1px solid $hover;
}

input:focus {
    border: 2px solid $hover;
}